//      ..


export default {
  required: function(message) {
      /*
         .
      :param message: - ,    .     false.
       ,   .
      :
      
      const validate = minLength(2, ' ');
      validate('') // true
      validate('') //  
      */
     return function(value) {
          if (!value) {
              return message || false
          }
          return true
      }
  },

  minLength(length, message) {
      /*
         
      :param length:  
      :param message: - ,    .     false.
       ,   .
      :
      
      const min = minLength(2, ' 2 .');
      min('12') // true
      min('1') //  2 
      */
      return function(value) {
          if (value.length <= length) {
              return message || false
          }
          return true
      }
  },

  maxLength(length, message) {
      /*
         
      :param length:  
      :param message: - ,    .     false.
       ,   .
      :
      
      const max = maxLength(3, ' 2 .');
      max('12') // true
      max('123') //  2 
      */
      return function(value) {
          if (value.length > length) {
              return message || false
          }
          return true
      }
  },

  emailMatch(message) {
      /*
       email.
      :param message: - ,    .     false.
       ,   .
      */
      return function(value) {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
          if (!pattern.test(value)) {
              return message || false
          }
          return true
      }
  },

  regexpMatch(pattern, message) {
      /*
          .
      :param message: - ,    .     false.
       ,   .
      */
      return function(value) {
          if (!pattern.test(value)) {
              return message || false
          }
          return true
      }
  },
}