export class BranchesClient extends ApiClient {
  /**
   *   API .
   *   list()     .
   */
  async list(status=null) {
    if (!status) {
      return super.list()
    }
    let response;
    try {
      response = await http.get(`${this.endpoint}/?status=${status}`)
    } catch (err) {
      throw errorUtils.checkHttpError(err)
    }
    return response
  }
}


